.home-layout {
    background-image: url('./img/background.png');
    background-size: cover;
    background-position: center center;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.home-layout .title {
    color: #fff;
    font-size: 120px;
    text-shadow:
    0 0 2px #fff,
    0 0 1px #fff,
    0 0 2px #fff,
    0 0 42px #f00,
    0 0 82px #f00,
    0 0 92px #f00,
    0 0 102px #f00,
    0 0 151px #f00;
}