.map-container {
    width: 700px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.map-tiles {
    display: grid;
    grid-template-columns: repeat(3, 200px);
    grid-template-rows: repeat(3, 200px);
    gap: 5px;
}

.map-area {
    width: 610px;
    height: 610px;
    overflow: hidden;
}

.map-tile {
    background-color: rgb(193, 193, 193);
    border: 3px solid rgb(81, 76, 76);
    padding: 5px;
    }

.wall {
background-image: url('./img/wall.jpeg');
}

.player {
    height: 50px;
    width: 38px;
    background: url('./img/players.png') no-repeat 0 0 / cover;
}
.tile-spin {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 50%;
    margin-left: auto;
    margin-right: auto;
}